<template>
  <div class="chMedicine">
    <medicine-options @search="search" ref="medicOptions"/>
    <medicine-table :loading="loading" @search="search" :data="data"/>
    <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
  </div>
</template>

<script>
import MedicineOptions from './controls/medicineOptions'
import MedicineTable from './controls/medicineTable'
import queryString from "querystring"
import page from 'data/page'

export default {
  name: 'chMedicine',
  mixins: [page],
  data() {
    return {
      data:[], //table数据
      currentPage: 1,
      loading: false
    }
  },
  components: {
    MedicineOptions,
    MedicineTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.medicOptions.getData();
      const tableParams = {
        size: 20,
        page: this.currentPage -1
      };
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.api.get(`/chinaMed?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.setPage(res.data)
      }).catch((err) => {
        console.log(err)
        this.$message(err.message)
      })
    }
  },
  watch: {
    currentPage() {
      this.search()
    }
  }
}
</script>
 
